"use client"

import React from 'react'
import { useTheme } from "next-themes"
import { Header } from '@/components/header'
import { Languages, Moon, Sun, BellRing, Shield, HelpCircle, Info } from 'lucide-react'
import { Button, Card, List } from 'antd';
import { useLanguage } from '@/lib/language-context'

export default function SettingsPage() {
  const { theme, setTheme } = useTheme()
  const { t } = useLanguage()

  const settingKeys = ['language', 'theme', 'notifications', 'privacy', 'help', 'about'];

  const settings = settingKeys.map(key => {
    switch (key) {
      case 'theme':
        return {
          key,
          title: t(theme === 'dark' ? 'light_mode' : 'dark_mode'),
          icon: theme === 'dark' ? <Sun /> : <Moon />,
          action: () => setTheme(theme === 'dark' ? 'light' : 'dark'),
        };
      default:
        return {
          key,
          title: t(key),
          icon: {
            language: <Languages />,
            notifications: <BellRing />,
            privacy: <Shield />,
            help: <HelpCircle />,
            about: <Info />,
          }[key],
          action: null,
        };
    }
  });

  return (
    <div>
      <Header />
      <div style={{ padding: '20px' }}>
        <List
          grid={{ gutter: 16, column: 2 }}
          dataSource={settings}
          renderItem={item => (
            <List.Item>
              <Card hoverable onClick={item.action ? () => item.action() : undefined}>
                <div className='flex items-center justify-center flex-col h-20'>
                  {item.icon}
                  <span className='mt-2'>{item.title}</span>
                </div>
              </Card>
            </List.Item>
          )}
        />
      </div>
    </div>
  )
}